import React from 'react'

import NavButton from './nav-button'
import {
  Twitter,
  Home,
  Explore,
  Notifications,
  Message,
  Bookmark,
  Lists,
  More,
  Profile
} from './icons'

import styles from './navigation.module.css'
function Navigation() {
  return (
    <button type="button" className={styles.button}>
      <nav className={styles.nav}>
        <NavButton>
          <Twitter />
        </NavButton>
        <NavButton>
          <Home />
          <span>Home</span>
        </NavButton>
        <NavButton>
          <Explore />
          <span>Explore</span>
        </NavButton>
        <NavButton>
          <Notifications />
          <span>Natifications</span>
        </NavButton>
        <NavButton>
          <Message />
          <span>Message</span>
        </NavButton>
        <NavButton>
          <Bookmark />
          <span>Bookmarks</span>
        </NavButton>
        <NavButton>
          <Lists />
          <span>Lists</span>
        </NavButton>
        <NavButton>
          <Profile />
          <span>Profile</span>
        </NavButton>
        <NavButton>
          <More />
          <span>More</span>
        </NavButton>
      </nav>
    </button>
  )
}

export default Navigation
